@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Maven+Pro:wght@400;500;700&display=swap');

// auto-resizing fonts 
html {
    font-size: 15px;
}

@media screen and (min-width: 400px) {
    html {
        font-size: calc(15px + 5 * ((100vw - 400px) / 1000));
    }
}

@media screen and (min-width: 1400px) {
    html {
        font-size: 20px;
    }
}


p {
    margin-bottom: 0;

}





//burger menu
.animated-icon1 span {
    background: $black-light;
}

.navbar {
    background-color: $white;
    padding: 15px;
    transition: .3s;
    color: #000;
    transition: .3s linear;
    min-height: 120px;

    @media(max-width:1199px) {
        min-height: 64px;
        padding: 10px 15px;
    }


    &.navbarHome {
        background-color: transparent;
        padding: 60px 15px 15px;

        @media(max-width:1199px) {
            padding: 15px;
            background-color: $white;
            min-height: 64px;
        }

        .nav-link {
            color: $white;
        }
    }

    &.nav-subpage {
        .nav-link {
            color: $black-light;
        }
    }




    .navbar-brand {
        padding-top: .2rem;
        padding-bottom: .2rem;

        img {
            max-height: 73px;
            width: auto;
            transition: .3s linear;
            max-width: 100%;

            @media(max-width:1199px) {
                max-height: 40px;
                width: auto;
            }
        }
    }




    ul#menu-menu-glowne {
        display: flex;
        align-items: center;

        .nav-item {
            &:last-child {
                @media(min-width:1200px) {
                    margin-left: 65px;
                }
            }

            .dropdown-menu {
                padding: 0;
                background-color: transparent;
                border: none;



                @media(min-width:1200px) {
                    background-color: rgba(255, 255, 255, .9);
                    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
                    overflow: hidden;
                }

                .nav-item {
                    padding: 0;
                    margin: 0;


                    .dropdown-item {
                        padding: .25rem .75rem;
                        font-size: .8rem;

                        @media(max-width: 1199px) {
                            text-align: center;
                            font-size: 1.1rem;

                        }

                        &:hover {
                            background-color: $red;
                            color: $white;
                        }
                    }
                }
            }
        }

    }

    &.scroll {
        background-color: #fff;
        padding: 10px 15px;
        min-height: 64px;

        #navbarNavDropdown {
            .nav-item {
                .nav-link {
                    color: $black-light;
                }
            }

        }

        .navbar-brand {


            img {
                max-height: 40px;
                width: auto;
            }
        }
    }



    #navbarNavDropdown {
        justify-content: flex-end;


        .nav-item {


            // add fast contact phone
            &.phone-desctop-fast-contact {
                height: 57px;
                width: 57px;
                background: red;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.16);
                cursor: pointer;

                img {
                    border: 1px solid $white;
                    border-radius: 50%;
                    padding: 10px;
                    max-width: 57px;
                    height: auto;
                }

                @media(max-width: 1199px) {
                    display: none;
                }


            }

            .nav-link {
                font-size: .8rem;
                padding-left: .5rem;
                padding-right: .5rem;
                margin-left: .5rem;
                margin-right: .5rem;
                position: relative;
                transition: .3s;
                text-transform: uppercase;

                @media(max-width:1360px) {
                    padding-left: .1rem;
                    padding-right: .1rem;
                }


                @media(max-width: 1199px) {
                    color: $black-light;
                    text-align: center;
                    font-size: 1.1rem;
                    font-weight: 500;
                }


                &:hover {
                    color: $red;
                }

                &.dropdown-toggle {
                    &:after {
                        border-top: .3em solid $red;
                    }
                }



            }

            .active {
                .nav-link {
                    color: $red;
                }
            }

            &.current_page_item {

                .nav-link {
                    color: $red;
                }
            }

        }

    }
}




//general 

html {
    scroll-behavior: smooth;
    height: 100%;
    min-height: 100%;
}

body {
    font-family: 'Maven Pro', sans-serif;
    color: $black-light;
    font-weight: 400;
    box-sizing: border-box;
    overflow-x: hidden;

    .number-desctop {
        position: fixed;

        right: 4%;
        z-index: 999;
        background: rgba(255, 255, 255, 0.66);
        padding: 10px;
        border-radius: 50px;
        opacity: 0;
        transition: .3s linear;
        transform: scale(0) translateY(0);

        @media(max-width:1900px) {
            right: 2%;
        }

        @media(max-width:1820px) {
            right: 3%;
        }

        @media(max-width:1730px) {
            right: 25px;
        }

        a {
            font-size: 1rem;
            font-weight: 500;
            color: $color-text-intro;
            min-width: 10rem;
            display: flex;
            justify-content: center;
        }

        &.active {
            display: block;
            transition: .3s linear;
            opacity: 1;
            transform: scale(1) translateY(150px);
            z-index: 9999;

            &.scroll {
                transform: scale(1) translateY(100px);
            }


            @media(max-width: 1199px) {
                display: none;
            }
        }
    }

    .phone-mobile-fast-contact {
        height: 57px;
        width: 57px;
        background: red;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.16);
        position: fixed;
        right: 15px;
        bottom: 45px;
        z-index: 9999999;
        display: none;
        transform: scale(1);
        animation: pulse 2s infinite;

        @media(max-width:1199px) {
            display: flex;
            background: $blue-dark;
        }

        img {
            border: 1px solid $white;
            border-radius: 50%;
            padding: 10px;
            max-width: 57px;
            height: auto;
        }
    }

}



a:hover {
    text-decoration: none;
}

.container {
    max-width: 1640px;

    &-smaller {
        max-width: 80%;
        margin: 0 auto;

        @media(max-width: 767px) {
            max-width: 100%;
        }
    }
}


.slider-home {
    min-height: 1080px;
    width: 100%;
    position: relative;

    @media(max-width:767px) {
        min-height: 745px;
    }

    .img-slider {
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        object-position: bottom center;
        height: 100%;
        width: 100%;
    }

    .apla-slider {
        padding: 80px 140px 55px;
        position: absolute;
        margin-top: 180px;
        left: 50%;
        transform: translateX(-50%);
        background: rgba(238, 249, 255, .66);
        width: 1085px;

        @media(max-width:1440px) {
            padding: 60px 90px 35px;
            width: 991px;
        }

        @media(max-width:1199px) {
            padding: 60px 30px 35px;
            width: 767px;
        }


        @media(max-width:991px) {
            width: 670px;
            padding: 60px 15px 35px;
        }

        @media(max-width:767px) {
            padding: 40px 5px 5px;
            width: calc(100% - 80px);
            margin: 117px auto 0;
        }



        @media(max-width:360px) {
            width: calc(100% - 30px);
        }

        .title-page {
            margin-bottom: 3rem;

            @media(max-width:767px) {
                margin-bottom: 15px;
            }

            h1 {
                font-size: 2rem;
                font-weight: 700;
                text-align: center;
                color: $blue-dark;
                margin-bottom: 0;

                @media(max-width:767px) {
                    font-size: 20px;
                }
            }
        }

        .text-on-slider {
            margin-bottom: 3rem;

            @media(max-width:767px) {
                margin-bottom: 20px;

            }

            p {
                margin-bottom: 0;
                text-align: center;
                font-size: 1.25rem;
                color: $color-text-intro;

                @media(max-width:767px) {
                    font-size: 15px;
                }
            }
        }

        .list-icon-apla-slider {
            margin-bottom: 90px;

            @media(max-width:767px) {
                margin-bottom: 5px;
            }

            .area-icon-text-apla-slider {
                display: flex;
                flex-direction: column;
                align-items: center;
                margin-bottom: 1.5rem;

                @media(max-width:767px) {
                    margin-bottom: 5px;
                }

                .item-icon-apla-slider {
                    width: 72px;
                    height: 72px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 5px;
                    border-radius: 15px;
                    margin-bottom: 1.5rem;

                    @media(max-width:767px) {
                        width: 64px;
                        height: 64px;
                        margin-bottom: 15px;

                    }

                    img {
                        max-width: 100%;
                        height: auto;
                        display: block;
                    }
                }

                .text-item-icon-apla-slider {
                    @media(max-width:767px) {
                        margin-bottom: 24px;
                    }

                    h3 {
                        font-size: $font-size-desc;
                        font-weight: 500;
                        text-align: center;
                        margin-bottom: 0;
                        color: $color-text-intro;

                        @media(max-width:767px) {
                            font-size: 12px;
                        }
                    }
                }

            }
        }

        .btn-apla-slider {
            background-color: $blue-light;
            height: 60px;
            display: flex;
            justify-content: center;
            align-items: center;
            color: $white;
            max-width: 260px;
            margin: 0 auto;
            transition: .3s linear;
            font-family: 'Lato', sans-serif;
            font-weight: 500;
            font-size: 1rem;

            &:hover {
                background-color: $red;
            }

            @media(max-width:767px) {
                display: none;
            }
        }

    }
}


.arrow-animate {
    display: flex;
    justify-content: center;
    animation: bounce-scroll 2.1s linear infinite;

    @media(max-width:767px) {
        animation: bounce-scroll-mobile 2.1s linear infinite;
    }
}

.aboutus_home {
    padding: 3rem 2rem 8.25rem;
    max-width: 1080px;
    margin: 0 auto;

    @media(max-width:767px) {
        padding: .5rem 2rem 3.25rem;
    }

    @media(max-width:413px) {
        padding: .5rem 0rem 3.25rem;
    }

    .title-section {
        padding: 3rem 0;

        @media(max-width:767px) {
            padding: 1.5rem 0;
        }

        h2 {
            color: $red;
            font-size: $title-section;
            font-weight: 700;
            margin-bottom: 0;
            text-align: center;

            @media(max-width:767px) {
                font-size: 18px;
            }
        }
    }

    .text-aboutus-home {
        h3 {
            margin-bottom: 2.25rem;
            text-align: center;
            font-size: 1.25rem;
            font-weight: 700;

            @media(max-width:767px) {
                font-size: 15px;
            }
        }

        p {
            margin-bottom: 2rem;
            text-align: center;
            font-size: 1.25rem;

            @media(max-width:767px) {
                font-size: 15px;
            }
        }
    }

    .buttons-aboutus-home {
        display: flex;
        justify-content: center;
        padding-top: 1rem;

        .btn-aboutus {
            background-color: $blue-light;
            margin: 0 15px;
            color: $white;
            width: 248px;
            height: 60px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .3s linear;
            font-family: 'Lato', sans-serif;
            font-weight: 500;
            font-size: 1rem;

            @media(max-width:767px) {
                font-size: 12px;
                width: 100%;
                height: 45px;
                margin: 0 8px;

                &:first-child {
                    margin-left: 0;
                }

                &:last-child {
                    margin-right: 0;
                }
            }

            &:hover {
                background-color: $red;
            }
        }
    }
}

section.offer {
    padding: 9rem 1.25rem 11rem;
    background-size: cover;
    background-position: center;
    min-height: 840px;

    @media(max-width:767px) {
        padding: 65px 0 0;
    }

    .title-section {
        margin-bottom: 6rem;

        @media(max-width:767px) {
            margin-bottom: 30px;
        }

        h2 {
            color: $white;
            font-size: $title-section;
            font-weight: 700;
            margin-bottom: 0;
            text-align: center;

            @media(max-width:767px) {
                font-size: 18px;
            }
        }
    }

    .block-item-offer-section-homepage {
        min-height: 273px;
        background: $white;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 30px;
        position: relative;

        @media(max-width:767px) {
            min-height: 160px;
            margin: 0 25px 30px;
        }

        @media(max-width:413px) {
            margin: 0 0 30px;
        }

        .icon {
            margin-bottom: 1.25rem;

            @media(max-width:767px) {
                padding: 5px;
            }



            img {
                max-width: 77px;
                height: auto;

                @media(max-width:767px) {
                    max-width: 64px;
                }
            }
        }

        .text-name-offer {
            h3 {
                text-align: center;
                font-size: 1.25rem;
                font-weight: 700;
                margin-bottom: 0;
                color: $black-light;

                @media(max-width:767px) {
                    font-size: 15px;
                }
            }
        }

        .hover-area-text {
            position: absolute;
            height: 100%;
            width: 100%;
            background: $blue-light;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            opacity: 0;
            transition: .1s linear;
            transition-delay: .4s;
            transform: scale(1);
            padding: 0 2rem;

            @media(max-width:1199px) {
                padding: 0 1rem;
            }

            p {
                margin-bottom: 0;
                color: $white;
                text-align: center;
                font-size: $font-size-desc;
                line-height: 24px;

                display: -webkit-box;
                -webkit-line-clamp: 6;
                -webkit-box-orient: vertical;
                overflow: hidden;
                text-overflow: ellipsis;

            }

            .readmore-text {
                position: absolute;
                right: 1.5rem;
                bottom: 1rem;
                color: $white;
                display: block;
                font-family: $lato;
                font-weight: 400;
                font-size: $font-size-desc;
                line-height: 22px;
            }

            &:hover {
                transform: scale(1.08);
                opacity: 1;

                @media(max-width:767px) {
                    opacity: 0;
                }
            }
        }

    }
}



.realizations {
    padding: 6.25em 1.25rem 3.5rem;

    @media(max-width:767px) {
        padding: 3.25em 1.25rem;
    }

    @media(max-width:413px) {
        padding: 3.25em 0;
    }

    .title-section {
        margin-bottom: 3rem;

        @media(max-width:767px) {
            margin-bottom: 30px;
        }

        h2 {
            color: $red;
            font-size: $title-section;
            font-weight: 700;
            margin-bottom: 0;
            text-align: center;

            @media(max-width:767px) {
                font-size: 18px;
            }
        }
    }


    .grid-container {
        display: grid;
        grid-template-columns: auto auto auto auto auto auto auto auto auto auto;
        grid-gap: 30px;
        padding: 10px;
        margin-bottom: 3rem;

        @media(max-width:991px) {
            grid-gap: 20px;
            margin-bottom: 1.5rem;
        }
    }

    .item1 {
        grid-column: 1 / 4;

        @media(max-width:1199px) {
            grid-column: 1 / 6;
        }

        @media(max-width:991px) {
            grid-column: 1 / 11;
        }

    }

    .item2 {
        grid-column: 4 / 11;

        @media(max-width:1199px) {
            grid-column: 6 / 11;
        }

        @media(max-width:991px) {
            grid-column: 1 / 11;
        }
    }


    .item3 {
        grid-column: 1 / 6;

        @media(max-width:1199px) {
            grid-column: 1 / 6;
        }

        @media(max-width:991px) {
            grid-column: 1 / 11;
        }
    }

    .item4 {
        grid-column: 6 / 11;

        @media(max-width:1199px) {
            grid-column: 6 / 11;
        }

        @media(max-width:991px) {
            grid-column: 1 / 11;
        }
    }



    .text-post-apla {
        height: 100%;
        width: 100%;
        background: linear-gradient(rgba(0, 112, 162, .66), rgba(0, 112, 162, .66));
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        transition: .2s linear;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 25px;

        @media(max-width:767px) {
            position: unset;
            height: unset;
            min-height: 75px;
            opacity: 1;
            padding: 15px 0 0 0;
            background: $white;
        }

        h3 {
            color: $white;
            font-size: 1rem;
            width: 100%;
            margin-bottom: 0;
            font-weight: 700;

            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;

            @media(max-width: 767px) {
                color: $color-text-intro;
                font-size: 15px;
            }

            &:after {
                content: '';
                display: block;
                width: 80%;
                background: $red;
                height: 2px;
                margin: 15px auto;
            }
        }

        p {
            font-size: 1rem;
            color: $white;
            margin-bottom: 0;

            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;

            @media(max-width: 767px) {
                color: $color-text-intro;
                font-size: 12px;
            }
        }
    }

    .grid-container>.link-post {
        text-align: center;
        font-size: 30px;
        height: 354px;
        position: relative;
        transition: .2s linear;
        transform: scale(1);


        @media(max-width:767px) {
            height: unset;
        }

        &:hover {
            transform: scale(.9);

            @media(max-width:767px) {
                transform: scale(1);
            }

            .text-post-apla {
                opacity: 1;
            }

        }

        .bg-block-realization {
            background-size: cover !important;
            background-position: bottom center !important;
            background-repeat: no-repeat !important;
            height: 100%;

            @media(max-width:767px) {
                height: 160px;
            }
        }
    }



}

