// animate arrow
@keyframes bounce-scroll {
    0% {
        transform: translateY(0);
    }

    10% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(25px);

    }

    50% {
        transform: translateY(0);
    }


    100% {
        transform: translateY(0);
    }
}


// animate arrow mobile
@keyframes bounce-scroll-mobile {
    0% {
        transform: translateY(0);
    }

    10% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(10px);

    }

    50% {
        transform: translateY(0);
    }

    100% {
        transform: translateY(0);
    }
}


// animation pulse
@keyframes pulse {
    0% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(79, 141, 221, 0.7);
    }

    70% {
        transform: scale(1);
        box-shadow: 0 0 0 10px rgba(46, 71, 185, 0);
    }

    100% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(62, 70, 185, 0);
    }
}


