                        width: 100%;
                        padding: 0;
                        max-width: 50%;
                        margin: 0 auto;
                    }

                    @media(max-width:360px) {
                        max-width: 65%;

                    }

                    a {
                        font-size: .8rem;
                        color: $color-text-intro;
                        line-height: 16px;
                        font-weight: 500;
                        transition: .3s linear;

                        &:hover {
                            color: $red;
                        }

                        @media(max-width:767px) {
                            font-size: 12px;
                        }
                    }

                    img {
                        margin-right: 15px;
                        max-width: 15px;
                        height: auto;
                    }
                }
            }

        }

        .area-menu-footer {
            padding-bottom: 2.5rem;
            border-bottom: 1px solid $red;
            max-width: 1080px;
            margin: 0 auto;

            @media(max-width:767px) {
                border-bottom: 1px solid $blue-dark;
                padding-bottom: 0;
            }

            .nav-footer {
                @media(max-width:767px) {
                    display: none;
                }

                ul {
                    margin: 0;
                    padding: 0;
                    display: flex;
                    justify-content: center;
                    flex-wrap: wrap;


                    li {
                        list-style: none;
                        margin: 0 0.375rem;

                        a {
                            font-size: 0.8rem;
                            font-weight: 500;
                            color: $color-text-intro;
                            transition: .3s linear;

                            &:hover {
                                color: $red;
                            }
                        }
                    }
                }
            }
        }

        .area-copyright {
            max-width: 1080px;
            margin: 0 auto;

            .link-to-redrocks {

                p,
                a {
                    color: $color-text-intro;
                    font-size: .7rem;
                    font-weight: 500;

                    @media(max-width: 767px) {
                        text-align: center;
                        font-size: 10px;
                    }
                }
            }
        }
    }
}


