// add cookies style 
.cli-bar-container {
    display: flex;
    align-items: center;


    .cli-bar-message {
        p {
            @media(max-width:985px) {
                text-align: center;
            }
        }

        a {
            color: $white;
        }


    }


    .cli-bar-btn_container {
        @media(max-width:985px) {

            display: flex;
            width: 100%;
            justify-content: center;
        }

        #cookie_action_close_header {
            border: none;

            &:focus {
                outline: -webkit-focus-ring-color auto 0px;
            }

            @media(max-width:985px) {
                width: 50%;
            }

            @media(max-width:767px) {
                width: 100%;
            }
        }
    }



